<!-- Tip 批次号Tabs标签页 -->
<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
        <el-form-item prop="nameOrCode" class="search-input-form-item">
          <el-input v-model="query.nameOrCode" placeholder="请输入批次号" class="filter-item" style="width: 100%;" clearable @keyup.enter.native="search" />
        </el-form-item>
        <el-button
          style="float: none !important;"
          class="filter-item"
          type="primary"
          icon="el-icon-search"
          @click="search"
        >查询</el-button>
      </el-form>
      <!-- Tip Main -->
      <!-- 拖拽屏幕 -->
      <template v-if="pageInfo">
        <drag-screen :extra-minus-height="140">
          <template #left>
            <el-card class="box-card" :body-style="{display: 'flex', justifyContent: 'space-between',width: '100%',boxSizing: 'border-box'}">
              <div class="img-box">
                <el-image :src="imgUrl" style="width: 100px;height: 100px;">
                  <div slot="placeholder" class="image-slot">
                    加载中<span class="dot">...</span>
                  </div>
                  <div slot="error" class="image-slot">
                    <i class="el-icon-picture-outline" />
                  </div>
                </el-image>
                <span v-if="pageInfo.fileIds" class="mask-layer">
                  <span @click="showBigPic([imgUrl])">预览</span>
                </span>
              </div>
              <el-descriptions :column="1" style="width: calc(100% - 130px);height: 130px;overflow: auto;">
                <el-descriptions-item label="物料编号">
                  {{ pageInfo.materialCode || '---' }}
                </el-descriptions-item>
                <el-descriptions-item label="物料名称">
                  {{ pageInfo.materialName || '---' }}
                </el-descriptions-item>
                <el-descriptions-item label="规格型号">
                  {{ pageInfo.spec || '---' }}
                </el-descriptions-item>
                <el-descriptions-item label="评估类型">
                  {{ pageInfo.assessType_dictText || '---' }}
                </el-descriptions-item>
                <el-descriptions-item label="物料组">
                  {{ pageInfo.materialGroup || '---' }}
                </el-descriptions-item>
                <el-descriptions-item label="物料描述">
                  {{ pageInfo.description || '---' }}
                </el-descriptions-item>
              </el-descriptions>
            </el-card>
          </template>
          <template #right>
            <el-card class="box-card-rt" shadow="never">
              <div slot="header" class="clearfix">
                <span>全部条码</span>
              </div>
              <table-com
                ref="baseTableRef"
                :refresh-key="refreshKey"
                :table-height-type="550"
                :loading="loading"
                :index="true"
                :index-fixed="false"
                :selection="false"
                :multiple="false"
                :selection-fixed="false"
                :table-column-sort="false"
                :table-data="pageInfo && pageInfo.barCodeList"
                :column-list="columnList"
                :expand-type="'table'"
                :expand-list="columnList1"
                :show-operate="showOperate"
                :operate-fixed="false"
                :table-selection="tableSelection"
                :operation-width="100"
                @selectionChange="selectionChange"
              >
                <template #expand-table="{scope}">
                  <el-table :data="scope.row.subBarCodeList" border stripe class="expand-table">
                    <el-table-column type="index" label="序号" fixed="left" align="center" />
                    <el-table-column v-for="(c, i) in columnList1" :key="i" :prop="c.prop" :label="c.label" :align="c.align" :width="c.width" />
                  </el-table>
                </template>
              </table-com>
            </el-card>
          </template>
        </drag-screen>
      </template>
      <template v-else>
        <el-empty class="el-empty-sty">
          <template slot="description">
            <el-button type="text" style="font-size: 23px;">
              暂无数据
            </el-button>
          </template>
        </el-empty>
      </template>
    </div>
    <!-- <package-to-s-k-u
      v-model="skuVisible"
      :title="'供应商条码'"
      :is-show-print-btn="false"
      :extra-info="{columnList: columnList1 || [], tableData: dialogInfo1.subBarCodeList || []}"
    /> -->
  </div>
</template>

<script>
import DragScreen from '@/components/DragScreen'
// import PackageToSKU from '@/components/ModalSelect/PackageToSKU'
import paginationPage from '@/mixins/paginationPage'
import { getBatchNoInfoApi } from '@/apis/modules/trace'
import { getUploadedFileListCommonApi } from '@/apis/modules/common'
export default {
  name: 'SearchResultBatchNo',
  components: {
    DragScreen
    // PackageToSKU
  },
  mixins: [paginationPage],
  props: {
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      title: this.$route.meta.title,
      query: {
        nameOrCode: ''
      },
      refreshKey: 1, // 表格唯一key
      showOperate: false, // 是否展示表格操作栏
      loading: false, // 表格loading
      tableData: [], // 表格数据
      tableSelection: [], // 表格选中数据
      columnList: [
        {
          module: 'link',
          prop: 'barCode',
          label: '条码号',
          align: 'center',
          minWidth: 200,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: (row) => {
            return `${row.barCode}
            ${this.pageInfo.deliveryReceiptType === '3' ? `<span style="color: #000;">(</span><span style="color: #5ab1ef;">${this.contractType[this.pageInfo.deliveryReceiptType].text}</span><span style="color: #000;">)</span>` : ''}`
          },
          headerAlign: 'center',
          handleClick: (row, index) => {
            this.$emit('switchBarcodeTabPanel', row)
          }
        },
        {
          module: 'word',
          prop: 'snNo',
          label: '序列号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'number',
          label: '数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnList1: [
        // TODO 这块后期说不定也得调整为追溯模式
        {
          module: 'word',
          prop: 'barCode',
          label: '条码号',
          align: 'center',
          minWidth: 200,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'snNo',
          label: '序列号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'number',
          label: '数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      pageInfo: null,
      imgUrl: '',
      contractType: {
        '1': {
          text: '采购',
          color: '#2ec7c9'
        },
        '2': {
          text: '外协',
          color: '#b6a2de'
        },
        '3': {
          text: '寄售',
          color: '#5ab1ef'
        },
        '4': {
          text: '成品',
          color: '#ffb980'
        }
      }
      // skuVisible: false,
      // dialogInfo1: {}
    }
  },
  computed: {
  },
  mounted() {
  },
  methods: {
    init() {
      const params = {
        ...this.extraInfo,
        ...this.query
      }
      console.log('批次号tabs标签页接口参数：', params)
      return new Promise((resolve, reject) => {
        getBatchNoInfoApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.pageInfo = data
              console.log('批次号tabs标签页接口响应：', data)
              if (data.fileIds) {
                this.getFileListByIds(data.fileIds)
              }
            }
            resolve(res)
          })
          .catch(err => {
            console.log('err', err)
            reject(err)
          })
      })
    },
    search() {
      if (!this.query.nameOrCode) {
        this.pageInfo = null
        return
      }
      this.init()
    },
    // Tip 表格复选框改变回调
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    // 获取文件列表
    getFileListByIds(fileIds) {
      return new Promise((resolve, reject) => {
        const ids = fileIds.split(',')
        getUploadedFileListCommonApi({
          ids
        }).then(res => {
          const { code, data } = res
          if (!code) {
            console.log(data)
            const [item] = data
            if (item) {
              this.imgUrl = `${process.env.VUE_APP_BASE_API_WMS}${item.fileUrl}`
              // this.getBase64(`${process.env.VUE_APP_BASE_API_WMS}${item.fileUrl}`, dataURL => {
              //   this.imgUrl = `${dataURL}`
              // })
            }
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 展示大图
    showBigPic(imgs = []) {
      console.log('imgs:', imgs)
      this.$viewerApi({
        options: {
          toolbar: false,
          navbar: false,
          title: false
        },
        images: [...imgs]
      })
    },
    // 图片(url)转换base64
    getBase64(url, callback) {
      const Img = new Image()
      let dataURL = ''
      Img.src = url + '?v=' + Math.random()
      Img.setAttribute('crossOrigin', 'Anonymous')
      Img.onload = function() {
        const canvas = document.createElement('canvas')
        const width = Img.width
        const height = Img.height
        canvas.width = width
        canvas.height = height
        canvas.getContext('2d').drawImage(Img, 0, 0, width, height)
        dataURL = canvas.toDataURL('image/jpeg', 1) // 可选取多种模式
        return callback ? callback(dataURL) : null // 回掉函数获取Base64编码
      }
    }
    // 调用
    // let imgUrl=‘https://www.baidu.com/img/bd_logo1.png’
    //  this.getBase64(imgUrl, dataURL => {
    //  console.log(dataURL:就是base64了)
    // });
  }
}
</script>

<style lang="scss" scoped>
::v-deep .search-input-form-item {
  min-width: 215px;
  .el-form-item__content {
    width: 100%;
  }
}
::v-deep .el-empty-sty {
  height: calc(100vh - 140px - 140px);
  line-height: calc(100vh - 140px - 140px);
  .el-empty__image {
    display: none;
  }
}
::v-deep .box-card-rt {
  border: none;
  .el-card__header {
    padding: 18px 20px 0;
    border: none;
  }
}
.img-box {
  // width: 100px;
  // height: 100px;
  position: relative;
  .image-slot {
    display: flex;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
  }
  .mask-layer {
    position: absolute;
    width: 100px;
    height: 100px;
    left: 0;
    top: 0;
    cursor: default;
    text-align: center;
    color: #fff;
    opacity: 0;
    font-size: 20px;
    background-color: rgba(0,0,0,.5);
    // transition: opacity .3s;
    .span {
      display: none;
      color: #FFF;
    }
  }
  &:hover {
    .mask-layer {
      opacity: 1;
      span {
        display: inline-block;
        font-size: 14px;
        cursor: pointer;
        margin-top: 36px;
      }
    }
  }
}
</style>

