<template>
  <div
    v-if="outWarehouse"
    v-loading="loading"
    class="main-box"
    element-loading-text="加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <el-card v-if="pageInfo && pageInfo.length" class="box-card-rt" shadow="never">
      <div slot="header" class="clearfix">
        <span>出库信息</span>
      </div>
      <el-descriptions v-for="(item, index) in pageInfo" :key="index" :column="3" style="margin-bottom: 15px;">
        <el-descriptions-item label="出库单号">
          <el-link v-if="item.billNumber" type="primary" :underline="false" @click="handleViewOutwarehouseOrder(item)">{{ item.billNumber }}</el-link>
          <span v-else>---</span>
        </el-descriptions-item>
        <el-descriptions-item label="单据类型">
          {{ item.billType_dictText || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="库管员">
          {{ item.storekeeper || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="出库时间">
          {{ item.outWarehouseDate || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="出库仓库">
          {{ item.storageName || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="出库位置">
          {{ item.positionName || '---' }}
        </el-descriptions-item>
      </el-descriptions>
    </el-card>
  </div>
  <el-empty v-else class="empty-sty">
    <template slot="description">
      <el-button type="text" style="font-size: 23px;">
        暂无出库信息
      </el-button>
    </template>
  </el-empty>
</template>

<script>
import { getBarCodeOutWarehouseApi } from '@/apis/modules/trace'
export default {
  name: 'Out',
  components: {
  },
  mixins: [],
  props: {
    extraInfo: {
      type: Object,
      default: () => ({})
    },
    outWarehouse: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      title: this.$route.meta.title,
      pageInfo: null,
      loading: false
    }
  },
  computed: {
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      const params = {
        ...this.extraInfo
      }
      console.log('条码号tabs标签页-条码出库信息接口参数：', params)
      this.loading = true
      return new Promise((resolve, reject) => {
        getBarCodeOutWarehouseApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.pageInfo = data
              console.log('条码号tabs标签页-条码出库信息接口响应：', data)
            }
            this.loading = false
            resolve(res)
          })
          .catch(err => {
            console.log('err', err)
            this.loading = false
            reject(err)
          })
      })
    },
    // Tip 跳到《出库单信息》页面
    handleViewOutwarehouseOrder({ billNumber = '', billType = '' }) {
      this.$router.push({
        name: 'OutBoundDelivery',
        params: {
          // Tip 订单号
          billNumber
        },
        query: {
          sourcePage: 'SearchResult'
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .box-card-rt {
  border: none;
  .el-card__header {
    padding: 18px 20px 0;
    border: none;
  }
}
.main-box {
  min-height: 620px;
  padding: 10px;
  box-sizing: border-box;
}
.empty-sty {
  height: 580px;
  line-height: 580px;
  padding: 0;
  ::v-deep .el-empty__image {
    display: none;
  }
}
</style>
