<template>
  <div v-show="inspect" class="main-box">
    <div
      v-loading="loading"
      element-loading-text="加载中"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)"
    >
      <template v-if="pageInfo && pageInfo.length">
        <el-card v-for="(itemInfo, index) in pageInfo" :key="index" class="box-card-rt" shadow="never">
          <div slot="header" class="clearfix">
            <span>检验单信息</span>
          </div>
          <el-descriptions :column="3">
            <el-descriptions-item label="检验单号">
              {{ itemInfo.inspectNo || '---' }}
            </el-descriptions-item>
            <el-descriptions-item label="检验单状态">
              <template v-if="itemInfo.status || itemInfo.status >= 0">
                <el-tag :type="statusMap[`${itemInfo.status}`]" effect="plain">
                  {{ statusChartMap[`${itemInfo.status}`] || '---' }}
                </el-tag>
              </template>
              <template v-else>---</template>
            </el-descriptions-item>
            <el-descriptions-item label="创建时间">
              {{ itemInfo.createDate || '---' }}
            </el-descriptions-item>
            <el-descriptions-item label="完成时间">
              {{ itemInfo.finishDate || '---' }}
            </el-descriptions-item>
            <el-descriptions-item label="检验数量">
              <template v-if="itemInfo.inspectNumber">
                {{ typeof itemInfo.inspectNumber === 'number' ? itemInfo.inspectNumber : '---' }}
              </template>
              <template v-else>---</template>
            </el-descriptions-item>
            <el-descriptions-item label="合格数量">
              <template v-if="itemInfo.qualifiedNumber">
                {{ typeof itemInfo.qualifiedNumber === 'number' ? itemInfo.qualifiedNumber : '---' }}
              </template>
              <template v-else>---</template>
            </el-descriptions-item>
          </el-descriptions>
          <div style="height: 1px;background-color: #ECEFF5;" />
          <div slot="default" class="clearfix" style="margin-top: 20px;">
            <span>条码号信息</span>
          </div>
          <el-descriptions :column="3" style="margin-top: 20px;">
            <el-descriptions-item label="检验方式">
              {{ itemInfo.detail.inspectTypeStr || '---' }}
            </el-descriptions-item>
            <el-descriptions-item label="检验结果">
              <el-tag :type="inspectResultStatusMap[`${itemInfo.detail.inspectResult}`]" effect="plain">
                {{ inspectResultMap[`${itemInfo.detail.inspectResult}`] || '---' }}
              </el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="批次号">
              {{ itemInfo.detail.batchNo || '---' }}
            </el-descriptions-item>
            <el-descriptions-item label="条码号">
              {{ itemInfo.detail.barCode || '---' }}
            </el-descriptions-item>
            <el-descriptions-item label="序列号">
              {{ itemInfo.detail.snNo || '---' }}
            </el-descriptions-item>
            <el-descriptions-item label="数量">
              {{ itemInfo.detail.number || '---' }}
            </el-descriptions-item>
          </el-descriptions>
          <div style="height: 1px;background-color: #ECEFF5;" />
          <div class="fl clearfix" style="margin: 20px 0;">
            <!-- Tip 需要控制按钮的操作状态 -->
            <el-button type="primary" :disabled="itemInfo.unqualifiedNumber <= 0">不合格品报告</el-button>
            <el-button type="primary" @click="handleOpenPDF">检验档案</el-button>
          </div>
        </el-card>
      </template>
      <el-empty v-else class="empty-sty">
        <template slot="description">
          <el-button type="text" style="font-size: 23px;">
            暂无检验信息
          </el-button>
        </template>
      </el-empty>
    </div>
  </div>
</template>

<script>
import { getBarCodeInspectApi } from '@/apis/modules/trace'
import { statusMap, statusChartMap, inspectResultStatusMap, inspectResultMap } from '@/views/in-storage/inspection/status'
export default {
  name: 'Inspection',
  components: {
  },
  mixins: [],
  props: {
    extraInfo: {
      type: Object,
      default: () => ({})
    },
    inspect: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      title: this.$route.meta.title,
      pageInfo: null,
      statusMap,
      statusChartMap,
      inspectResultStatusMap,
      inspectResultMap,
      loading: false
    }
  },
  computed: {
  },
  mounted() {
    this.init()
  },
  dicts: [],
  methods: {
    init() {
      const params = {
        ...this.extraInfo
      }
      console.log('条码号tabs标签页-检验信息接口参数：', params)
      this.loading = true
      return new Promise((resolve, reject) => {
        getBarCodeInspectApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.pageInfo = data
              console.log('条码号tabs标签页-检验信息接口响应：', data)
            }
            this.loading = false
            resolve(res)
          })
          .catch(err => {
            console.log('err', err)
            this.loading = false
            reject(err)
          })
      })
    },
    handleOpenPDF() {
      window.open(process.env.VUE_APP_BASE_API_WMS + this.pageInfo.report)
    }
  }
}
</script>

<style lang="scss" scoped>
.main-box {
  min-height: 620px;
  padding: 10px;
  box-sizing: border-box;
}

::v-deep .box-card-rt {
  .el-card__header {
    padding: 18px 20px 0;
    border: none;
  }
  margin-bottom: 10px;
}

.empty-sty {
  height: 580px;
  line-height: 580px;
  padding: 0;
  ::v-deep .el-empty__image {
    display: none;
  }
}
</style>
