<template>
  <div
    v-if="receipt"
    v-loading="loading"
    class="main-box"
    element-loading-text="加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <template v-if="pageInfo">
      <el-card class="box-card-rt" shadow="never">
        <div slot="header" class="clearfix">
          <span>采购订单信息</span>
        </div>
        <el-descriptions :column="3">
          <el-descriptions-item label="采购订单号">
            <el-link v-if="pageInfo.orderCode" type="primary" :underline="false" @click="handleViewPurchaseOrder">{{ pageInfo.orderCode }}</el-link>
            <span v-else>---</span>
          </el-descriptions-item>
          <el-descriptions-item label="供应商">
            {{ pageInfo.supplierName || '---' }}
          </el-descriptions-item>
          <el-descriptions-item label="订单金额">
            {{ pageInfo.orderAmount || '---' }}
          </el-descriptions-item>
          <el-descriptions-item label="采购日期">
            {{ pageInfo.purchaseDate || '---' }}
          </el-descriptions-item>
          <el-descriptions-item label="用料单位">
            {{ pageInfo.materialsUnit || '---' }}
          </el-descriptions-item>
        </el-descriptions>
      </el-card>
      <el-card class="box-card-rc" shadow="never">
        <div slot="header" class="clearfix">
          <span>交接单信息</span>
          <el-button type="text" class="fr" @click="handleViewOrder">查看此采购订单下全部交接单</el-button>
        </div>
        <el-descriptions :column="3">
          <el-descriptions-item label="交接单号">
            {{ pageInfo.receiptNo || '' }}
          </el-descriptions-item>
          <el-descriptions-item label="收货人">
            {{ pageInfo.receiver || '---' }}
          </el-descriptions-item>
          <el-descriptions-item label="物料到期时间">
            {{ pageInfo.materialExpire || '---' }}
          </el-descriptions-item>
          <el-descriptions-item label="交货时间">
            {{ pageInfo.deliveryDate || '---' }}
          </el-descriptions-item>
          <el-descriptions-item label="是否检验">
            {{ pageInfo.inspect || '---' }}
          </el-descriptions-item>
          <el-descriptions-item label="交接单状态">
            <template v-if="pageInfo.status || pageInfo.status >= 0">
              <el-tag :type="statusMap[`${pageInfo.status}`]" effect="plain">
                {{ statusChartMap[`${pageInfo.status}`] }}
              </el-tag>
            </template>
            <template v-else>---</template>
          </el-descriptions-item>
        </el-descriptions>
      </el-card>
      <el-card class="box-card-rb" shadow="never">
        <div slot="header" class="clearfix">
          <span>批次信息</span>
        </div>
        <el-descriptions :column="4">
          <el-descriptions-item label="批次号">
            <el-link type="primary" @click="handleviewBatchNo">{{ pageInfo.batchNo || '---' }}</el-link>
          </el-descriptions-item>
          <el-descriptions-item label="到货数量">
            {{ pageInfo.deliveryNumber || '---' }}
          </el-descriptions-item>
          <el-descriptions-item label="计量单位">
            {{ pageInfo.unitName || '---' }}
          </el-descriptions-item>
          <el-descriptions-item label="条码数量">
            {{ pageInfo.barCodeNumber || '---' }}
          </el-descriptions-item>
        </el-descriptions>
      </el-card>
      <el-card class="box-card-rb" shadow="never">
        <div slot="header" class="clearfix">
          <span>全部条码</span>
        </div>
        <el-scrollbar v-if="pageInfo.barCodesAndMaterialNum.length" style="height: 200px;">
          <el-descriptions :column="2">
            <template v-for="(c,i) in pageInfo.barCodesAndMaterialNum">
              <!-- <el-descriptions-item :key="i" :label="`${c.barcodeType === 1 ? '包装号' : '条码号'}`" :label-style="{backgroundColor: '#67C23A', color: `${c.barcodeType === 1 ? '#FFF' : '#5AB1EF'}`}"> -->
              <el-descriptions-item :key="i" :label="`${c.barcodeType === 1 ? '包装号' : '条码号'}`">
                <!-- 不存在最小单位SKU条码 -->
                <el-link v-if="c.subBarCodeList && Array.isArray(c.subBarCodeList) && !c.subBarCodeList.length" type="primary" @click="$emit('switchBarcode', {barCode: c.barCode})">
                  {{ c.barCode }}
                  <template v-if="c.deliveryReceiptType === '3'">
                    <span style="color: #000;">(</span>
                    <span :style="{color: contractType[c.deliveryReceiptType].color}">{{ `${contractType[c.deliveryReceiptType].text}` }}</span>
                    <span style="color: #000;">)</span>
                  </template>
                </el-link>
                <!-- 存在最小单位SKU条码 -->
                <el-popover
                  v-else
                  placement="top-start"
                  title=""
                  :width="450"
                  trigger="hover"
                >
                  <table-com
                    :ref="'minSkuBarcode'"
                    :loading="false"
                    :index="true"
                    :index-fixed="false"
                    :selection="false"
                    :selection-fixed="true"
                    :table-column-sort="false"
                    :table-height-type="300"
                    :table-data="c.subBarCodeList"
                    :show-operate="false"
                    :operate-fixed="false"
                    :operation-width="120"
                    :column-list="columnList"
                    :table-selection="[]"
                    :row-key="'barCode'"
                  />
                  <el-link slot="reference" type="primary" @click="$emit('switchBarcode', {barCode: c.barCode})">
                    {{ c.barCode }}
                    <template v-if="c.deliveryReceiptType === '3'">
                      <span style="color: #000;">(</span>
                      <span :style="{color: contractType[c.deliveryReceiptType].color}">{{ `${contractType[c.deliveryReceiptType].text}` }}</span>
                      <span style="color: #000;">)</span>
                    </template>
                  </el-link>
                </el-popover>
              </el-descriptions-item>
              <!-- <el-descriptions-item :key="i" label="物料数量" :content-style="{position: 'relative'}">
              {{ c.num || '---' }}
              <div v-if="(i+1) % 2 !== 0 && pageInfo.barCodesAndMaterialNum.length > 1" style="position: absolute;right: -3px;top: 0;height: 100%;width: 5px;border-left: 2px solid #409EFF;border-right: 2px solid #409EFF;" />
            </el-descriptions-item> -->
              <el-descriptions-item :key="i" label="物料数量">
                {{ c.num || '---' }}
              </el-descriptions-item>
            </template>
          </el-descriptions>
        </el-scrollbar>
        <div v-else style="font-size: 23px;text-align: center;">暂无条码</div>
      </el-card>
      <!-- <div>
        <el-button type="text" :icon="!showBarcode ? 'el-icon-arrow-down' : 'el-icon-arrow-up'" style="color: #101010;" @click="showBarcode = !showBarcode">全部条码</el-button>
        <div v-if="showBarcode" class="barcode-box">
          <template v-if="pageInfo.barCodes && pageInfo.barCodes.length">
            <el-link v-for="(item, i) in pageInfo.barCodes" :key="i" type="primary" @click="$emit('switchBarcode', {barCode: item})">
              {{ item }}
            </el-link>
          </template>
          <template v-else>
            <div style="font-size: 23px;text-align: center;">暂无条码</div>
          </template>
        </div>
      </div> -->
    </template>
    <!-- <template v-else>
      <el-card class="box-card-rt-empty" shadow="never">
        <div slot="header" class="clearfix">
          <span>交接信息</span>
        </div>
        暂无交接信息
      </el-card>
    </template> -->
  </div>
  <el-empty v-else class="empty-sty">
    <template slot="description">
      <el-button type="text" style="font-size: 23px;">
        暂无交接信息
      </el-button>
    </template>
  </el-empty>
</template>

<script>
import { getBarCodeReceiptApi, getBarCodePurchaseApi } from '@/apis/modules/trace'
import { statusChartMap, statusMap } from '@/views/in-storage/purchase/status'
import { requestWrapper } from '@/utils/utils'
import paginationPage from '@/mixins/paginationPage'
export default {
  name: 'Receipt',
  components: {
  },
  mixins: [paginationPage],
  props: {
    extraInfo: {
      type: Object,
      default: () => ({})
    },
    receipt: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      title: this.$route.meta.title,
      showBarcode: false,
      pageInfo: null,
      statusChartMap,
      statusMap,
      barCodePurchaseInfo: null,
      loading: false,
      contractType: {
        '1': {
          text: '采购',
          color: '#2ec7c9'
        },
        '2': {
          text: '外协',
          color: '#b6a2de'
        },
        '3': {
          text: '寄售',
          color: '#5ab1ef'
        },
        '4': {
          text: '成品',
          color: '#ffb980'
        }
      },
      columnList: [
        {
          module: 'word',
          prop: 'barCode',
          label: '条码号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'num',
          label: '物料数量',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ]
    }
  },
  computed: {
  },
  mounted() {
    this.init()
    this.getBarCodePurchaseInfo()
  },
  methods: {
    init() {
      const params = {
        ...this.extraInfo
      }
      console.log('条码号tabs标签页-交接信息接口参数：', params)
      this.loading = true
      return new Promise((resolve, reject) => {
        getBarCodeReceiptApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.pageInfo = data
              console.log('条码号tabs标签页-交接信息接口响应：', data)
            }
            this.loading = false
            resolve(res)
          })
          .catch(err => {
            console.log('err', err)
            this.loading = false
            reject(err)
          })
      })
    },
    // Tip 获取采购信息(主要是要拿到合同号，从而跳转采购订单页，定位左侧树的订单节点)
    async getBarCodePurchaseInfo() {
      const params = {
        ...this.extraInfo
      }
      const res = await requestWrapper(getBarCodePurchaseApi(params))
      if (!res.success) return
      const { data = null } = res
      this.barCodePurchaseInfo = data
    },
    // Tip 查看采购订单
    handleViewPurchaseOrder() {
      this.$router.push({
        name: 'InStoragePurchase',
        params: {
          // Tip 合同编号
          contractCode: this.barCodePurchaseInfo.contractCode || '',
          // Tip 采购订单号
          orderCode: this.pageInfo.orderCode || ''
        }
      })
    },
    // Tip 查看此采购订单下全部交接单
    handleViewOrder() {
      this.$router.push({
        name: 'InStoragePurchaseDelivery',
        params: {
          id: this.pageInfo.orderId, // 订单id
          type: this.pageInfo.orderType // 订单类型
        }
      })
    },
    // 查看批次号相关信息
    handleviewBatchNo() {
      console.log(7890, this.$listeners)
      this.$emit('switchBatchNoTabPanel', { batchNo: this.pageInfo.batchNo })
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .box-card-rt {
  border-width: 0 0 1px 0;
  .el-card__header {
    padding: 18px 20px 0;
    border: none;
  }
}
::v-deep .box-card-rt-empty {
  border: none;
  .el-card__header {
    padding: 18px 20px 0;
    border: none;
  }
}
::v-deep .box-card-rc {
  margin-top: 20px;
  border: none;
  .el-card__header {
    padding: 0 20px;
    border: none;
  }
}
::v-deep .box-card-rb {
  border: none;
  .el-card__header {
    padding: 0 20px;
    border: none;
  }
}
.barcode-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 5px;
  .el-link {
    margin-bottom: 15px;
  }
}
.main-box {
  min-height: 620px;
  padding: 10px;
  box-sizing: border-box;
}
.empty-sty {
  height: 580px;
  line-height: 580px;
  padding: 0;
  ::v-deep .el-empty__image {
    display: none;
  }
}
</style>

