<template>
  <div
    v-if="purchase"
    v-loading="loading"
    class="main-box"
    element-loading-text="加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <template v-if="pageInfo">
      <el-card class="box-card-rt" shadow="never">
        <div slot="header" class="clearfix">
          <span>合同信息</span>
        </div>
        <el-descriptions :column="2">
          <el-descriptions-item label="合同号">
            {{ pageInfo.contractCode || '---' }}
          </el-descriptions-item>
          <el-descriptions-item label="创建时间">
            {{ pageInfo.contractDate || '---' }}
          </el-descriptions-item>
          <el-descriptions-item label="供应商编号">
            {{ pageInfo.supplierCode || '---' }}
          </el-descriptions-item>
          <el-descriptions-item label="供应商">
            {{ pageInfo.supplierName || '---' }}
          </el-descriptions-item>
        </el-descriptions>
      </el-card>
      <el-card class="box-card-rb" shadow="never">
        <div slot="header" class="clearfix">
          <span>所属采购订单信息</span>
          <!-- 如果订单类型是外协跟寄售，就不进行跳转 -->
          <el-link :disabled="['2', '3'].includes(extraInfo.materialInfo.deliveryReceiptType)" :type="['2', '3'].includes(extraInfo.materialInfo.deliveryReceiptType) ? 'info' : 'primary'" :underline="false" class="fr" @click="handleViewOrder">查看此合同下全部采购订单</el-link>
        </div>
        <el-descriptions>
          <el-descriptions-item label="采购订单号">
            <!-- 如果订单类型是外协跟寄售，就不进行跳转 -->
            <el-link v-if="pageInfo.orderCode" :disabled="['2', '3'].includes(extraInfo.materialInfo.deliveryReceiptType)" :type="['2', '3'].includes(extraInfo.materialInfo.deliveryReceiptType) ? 'info' : 'primary'" :underline="false" @click="handleViewPurchaseOrder">{{ pageInfo.orderCode }}</el-link>
            <span v-else>---</span>
          </el-descriptions-item>
          <el-descriptions-item label="采购日期">
            {{ pageInfo.purchaseDate || '---' }}
          </el-descriptions-item>
          <el-descriptions-item label="用料单位">
            {{ pageInfo.materialsUnit || '---' }}
          </el-descriptions-item>
        </el-descriptions>
        <table-com
          ref="baseTableRef"
          :cell-style="handleCellStyle"
          :refresh-key="refreshKey"
          :table-height-type="350"
          :loading="false"
          :index="true"
          :index-fixed="false"
          :selection="false"
          :multiple="false"
          :selection-fixed="false"
          :table-column-sort="false"
          :table-data="pageInfo.orderMaterial || []"
          :column-list="columnList"
          :show-operate="showOperate"
          :operate-fixed="false"
          :table-selection="tableSelection"
          :operation-width="100"
          @selectionChange="selectionChange"
        />
      </el-card>
    </template>
    <!-- <template v-else>
      <el-card class="box-card-rt-empty" shadow="never">
        <div slot="header" class="clearfix">
          <span>采购信息</span>
        </div>
        暂无采购信息
      </el-card>
    </template> -->
  </div>
  <el-empty v-else class="empty-sty">
    <template slot="description">
      <el-button type="text" style="font-size: 23px;">
        暂无采购信息
      </el-button>
    </template>
  </el-empty>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getBarCodePurchaseApi } from '@/apis/modules/trace'
export default {
  name: 'Purchase',
  components: {
  },
  mixins: [paginationPage],
  props: {
    extraInfo: {
      type: Object,
      default: () => ({})
    },
    purchase: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      title: this.$route.meta.title,
      refreshKey: 1, // 表格唯一key
      showOperate: false, // 是否展示表格操作栏
      loading: false,
      tableSelection: [], // 表格选中数据
      columnList: [
        {
          module: 'word',
          prop: 'materialCode',
          label: '物料编号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '物料名称',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialSpec',
          label: '规格型号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialAssessType_dictText',
          label: '评估类型',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'orderNum',
          label: '订单数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'price',
          label: '单价',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitMaterial',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      pageInfo: null
    }
  },
  computed: {
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      const params = {
        ...this.extraInfo
      }
      console.log('条码号tabs标签页-采购信息接口参数：', params)
      this.loading = true
      return new Promise((resolve, reject) => {
        getBarCodePurchaseApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.pageInfo = data
              console.log('条码号tabs标签页-采购信息接口响应：', data)
            }
            this.loading = false
            resolve(res)
          })
          .catch(err => {
            console.log('err', err)
            this.loading = false
            reject(err)
          })
      })
    },
    // Tip 点击步骤
    handleStepClick(step) {},
    // Tip 查看合同下的采购订单
    handleViewOrder() {
      this.$router.push({
        name: 'InStoragePurchase',
        params: {
          // Tip 采购订单合同编号
          contractCode: this.pageInfo.contractCode || ''
        }
      })
    },
    // Tip 查看采购订单
    handleViewPurchaseOrder() {
      this.$router.push({
        name: 'InStoragePurchase',
        params: {
          // Tip 合同编号
          contractCode: this.pageInfo.contractCode || '',
          // Tip 采购订单号
          orderCode: this.pageInfo.orderCode || ''
        }
      })
    },
    // Tip 表格复选框改变回调
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    handleCellStyle({ row }) {
      const cellStyle = {
        backgroundColor: row.color
      }
      return cellStyle
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .box-card-rt {
  border-width: 0 0 1px 0;
  .el-card__header {
    padding: 18px 20px 0;
    border: none;
  }
}
::v-deep .box-card-rt-empty {
  border: none;
  .el-card__header {
    padding: 18px 20px 0;
    border: none;
  }
}
::v-deep .box-card-rb {
  margin-top: 20px;
  border: none;
  .el-card__header {
    padding: 0px 20px;
    border: none;
  }
}
.main-box {
  min-height: 620px;
  padding: 10px;
  box-sizing: border-box;
}
.empty-sty {
  height: 580px;
  line-height: 580px;
  padding: 0;
  ::v-deep .el-empty__image {
    display: none;
  }
}
</style>

