<!-- Tip 物料Tabs标签页 -->
<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
        <el-form-item prop="nameOrCode" class="search-input-form-item">
          <el-input v-model="query.nameOrCode" placeholder="请输入物料编号" class="filter-item" style="width: 100%;" clearable @keyup.enter.native="search" />
        </el-form-item>
        <el-button
          style="float: none !important;"
          class="filter-item"
          type="primary"
          icon="el-icon-search"
          @click="search"
        >查询</el-button>
      </el-form>
      <!-- Tip Main -->
      <!-- 拖拽屏幕 -->
      <template v-if="pageInfo">
        <drag-screen :extra-minus-height="140">
          <template #left>
            <el-card v-for="(cardInfo, index) in pageInfo" :key="index" class="box-card" :body-style="{display: 'flex', justifyContent: 'space-between',width: '100%',boxSizing: 'border-box',border: cardIndex === index ? '1px solid #1890ff' : ''}">
              <div class="img-box">
                <el-image :src="fileInfo[cardInfo.fileIds]" style="width: 100px;height: 100px;">
                  <div slot="placeholder" class="image-slot">
                    加载中<span class="dot">...</span>
                  </div>
                  <div slot="error" class="image-slot">
                    <i class="el-icon-picture-outline" />
                  </div>
                </el-image>
                <span v-if="cardInfo.fileIds" class="mask-layer">
                  <span @click="showBigPic([fileInfo[cardInfo.fileIds]])">预览</span>
                </span>
                <div style="text-align: right;">
                  <el-button type="text" @click="handleCardClick(index, cardInfo)">点我查看</el-button>
                </div>
              </div>
              <el-descriptions :column="1" style="width: calc(100% - 130px);height: 130px;overflow: auto;">
                <el-descriptions-item label="物料编号">{{ cardInfo.materialCode || '---' }}</el-descriptions-item>
                <el-descriptions-item label="物料名称">{{ cardInfo.materialName || '---' }}</el-descriptions-item>
                <el-descriptions-item label="规格型号">{{ cardInfo.spec || '---' }}</el-descriptions-item>
                <el-descriptions-item label="评估类型">{{ cardInfo.assessType_dictText || '---' }}</el-descriptions-item>
                <el-descriptions-item label="物料组">{{ cardInfo.materialGroup || '---' }}</el-descriptions-item>
                <el-descriptions-item label="物料描述">{{ cardInfo.description || '---' }}</el-descriptions-item>
              </el-descriptions>
            </el-card>
          </template>
          <template #right>
            <template v-if="rightInfo">
              <el-card class="box-card-rt" :body-style="{display: 'flex', justifyContent: 'space-between',width: '100%',boxSizing: 'border-box'}" shadow="never">
                <div slot="header" class="clearfix">
                  <span>合同信息</span>
                </div>
                <!-- Tip 树节点 -->
                <el-tree
                  v-if="showTree"
                  :key="treeUpdateKey"
                  v-loading="treeLoading"
                  class="el-tree-sty"
                  empty-text="当前树节点为空"
                  node-key="id"
                  :props="treeProps"
                  :load="loadNode"
                  :indent="18"
                  :expand-on-click-node="false"
                  lazy
                  highlight-current
                  check-on-click-node
                  @node-click="handleNodeClick"
                />
                <!-- Tip 合同信息 -->
                <el-descriptions v-if="nodeLevel === 1" :column="1" style="width: 50%;min-height: 130px;overflow: auto;">
                  <el-descriptions-item label="合同编号">{{ checkedNodeLevelData[nodeLevel].code || '---' }}</el-descriptions-item>
                  <el-descriptions-item label="创建日期">{{ checkedNodeLevelData[nodeLevel].createDate || '---' }}</el-descriptions-item>
                  <el-descriptions-item label="供应商编号">{{ checkedNodeLevelData[nodeLevel].supplierCode || '---' }}</el-descriptions-item>
                  <el-descriptions-item label="供应商">{{ checkedNodeLevelData[nodeLevel].supplierName || '---' }}</el-descriptions-item>
                </el-descriptions>
                <!-- Tip 订单信息 -->
                <el-descriptions v-if="nodeLevel === 2" :column="1" style="width: 50%;min-height: 130px;overflow: auto;">
                  <el-descriptions-item label="订单编号">{{ checkedNodeLevelData[nodeLevel].code || '---' }}</el-descriptions-item>
                  <el-descriptions-item label="采购日期">{{ checkedNodeLevelData[nodeLevel].purchaseDate || '---' }}</el-descriptions-item>
                  <el-descriptions-item label="供应商">{{ checkedNodeLevelData[nodeLevel].supplierName || '---' }}</el-descriptions-item>
                  <el-descriptions-item label="用料单位">{{ checkedNodeLevelData[nodeLevel].materialsUnit || '---' }}</el-descriptions-item>
                  <el-descriptions-item label="订单金额">{{ checkedNodeLevelData[nodeLevel].orderAmount || '---' }}</el-descriptions-item>
                  <el-descriptions-item label="订单状态">{{ checkedNodeLevelData[nodeLevel].status || '---' }}</el-descriptions-item>
                </el-descriptions>
                <!-- Tip 交接单信息 -->
                <el-descriptions v-if="nodeLevel === 3" :column="1" style="width: 50%;min-height: 130px;overflow: auto;">
                  <el-descriptions-item label="交接单号">{{ checkedNodeLevelData[nodeLevel].code || '---' }}</el-descriptions-item>
                  <el-descriptions-item label="到货日期">{{ checkedNodeLevelData[nodeLevel].deliveryDate || '---' }}</el-descriptions-item>
                  <el-descriptions-item label="仓库">{{ checkedNodeLevelData[nodeLevel].storageName || '---' }}</el-descriptions-item>
                  <el-descriptions-item label="收货人">{{ checkedNodeLevelData[nodeLevel].receiver || '---' }}</el-descriptions-item>
                  <el-descriptions-item label="交接单状态">
                    <el-tag v-if="checkedNodeLevelData[nodeLevel].status || checkedNodeLevelData[nodeLevel].status >= 0" :type="statusMap[checkedNodeLevelData[nodeLevel].status]" effect="plain">
                      {{ statusChartMap[checkedNodeLevelData[nodeLevel].status] }}
                    </el-tag>
                    <span v-else>---</span>
                  </el-descriptions-item>
                  <el-descriptions-item label="是否检验">{{ checkedNodeLevelData[nodeLevel].inspect }}</el-descriptions-item>
                  <el-descriptions-item label="检验单号">{{ checkedNodeLevelData[nodeLevel].inspectNo || '---' }}</el-descriptions-item>
                  <el-descriptions-item label="入库单号">
                    <div v-if="checkedNodeLevelData[nodeLevel].stockInList.length" class="barcode-box1">
                      <el-link v-for="(stockIn, index) in checkedNodeLevelData[nodeLevel].stockInList" :key="index" type="primary" style="width: 150px;text-align: center;" @click="toInWarehouseDetail(stockIn)">{{ stockIn.stockInBillNumber }}</el-link>
                    </div>
                    <span v-else>
                      ---
                    </span>
                  </el-descriptions-item>
                </el-descriptions>
              </el-card>
              <el-descriptions :label-style="{textAlign: 'center'}" :content-style="{textAlign: 'center'}" title="" direction="vertical" :column="4" border>
                <el-descriptions-item label="未到货数量">{{ rightInfo.inTransitNum || '---' }}</el-descriptions-item>
                <el-descriptions-item label="未到货金额">{{ rightInfo.inTransitAmount || '---' }}</el-descriptions-item>
                <el-descriptions-item label="库存数量">{{ rightInfo.inventoryNum || '---' }}</el-descriptions-item>
                <el-descriptions-item label="库存金额">{{ rightInfo.inventoryAmount || '---' }}</el-descriptions-item>
              </el-descriptions>
              <el-card class="box-card-rb" shadow="never">
                <div slot="header" class="clearfix">
                  <span>批次信息</span>
                </div>
                <div>
                  <el-button type="text" style="color: #101010;" @click="showBatchNo = !showBatchNo">
                    <span :class="[{expanded: showBatchNo}, 'el-icon-caret-right']" />
                    全部批次
                  </el-button>
                  <div v-if="showBatchNo" class="barcode-box">
                    <el-link v-for="(batchNo, index) in batchNoList" :key="index" type="primary" @click="$emit('switchBatchNoTabPanel', { batchNo })">{{ batchNo }}</el-link>
                  </div>
                </div>
              </el-card>
            </template>
          </template>
        </drag-screen>
      </template>
      <template v-else>
        <el-empty class="el-empty-sty">
          <template slot="description">
            <el-button type="text" style="font-size: 23px;">
              暂无数据
            </el-button>
          </template>
        </el-empty>
      </template>
    </div>
  </div>
</template>

<script>
import DragScreen from '@/components/DragScreen'
import { getMaterialInfoApi, getStepContractApi, getStepOrderApi, getBatchNoListApi } from '@/apis/modules/trace'
import { getUploadedFileListCommonApi } from '@/apis/modules/common'
import { requestWrapper } from '@/utils/utils'
import { statusMap, statusChartMap } from '@/views/in-storage/purchase/status'
export default {
  name: 'SearchResultMaterial',
  components: {
    DragScreen
  },
  mixins: [],
  props: {
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      title: this.$route.meta.title,
      query: {
        nameOrCode: ''
      },
      showBatchNo: false,
      treeProps: {
        children: 'children',
        label: 'code',
        isLeaf: (data) => {
          return !data.hasleaf
        }
      },
      treeLoading: false,
      showTree: true,
      pageInfo: null,
      nodeLevel: 0,
      checkedNodeLevelData: {
        1: {},
        2: {},
        3: {}
      },
      treeUpdateKey: 0,
      statusMap,
      statusChartMap,
      batchNoList: [], // 批次号列表
      cardIndex: '',
      rightInfo: null,
      fileInfo: {} // 文件信息{物料信息的fileIds字段值: 图片地址数组}
    }
  },
  computed: {
  },
  mounted() {
  },
  methods: {
    init() {
      const params = {
        ...this.extraInfo,
        ...this.query
      }
      this.showTree = false
      console.log('物料tabs标签页接口参数：', params)
      return new Promise((resolve, reject) => {
        getMaterialInfoApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.pageInfo = data
              const fileIdsArr = data.filter(c => c.fileIds).map(v => v.fileIds)
              const promiseArr = fileIdsArr.map(b => this.getFileListByIds(b))
              Promise.all(promiseArr)
                .then(arr => {
                  arr.forEach((res, i) => {
                    const dataKey = fileIdsArr[i]
                    this.fileInfo[dataKey] = res
                  })
                })
            }
            resolve(res)
          })
          .catch(err => {
            console.log('err', err)
            reject(err)
          })
      })
    },
    search() {
      if (!this.query.nameOrCode) {
        this.pageInfo = null
        return
      }
      this.init()
    },
    // 卡片点击回调
    handleCardClick(index, data) {
      console.log('data数据：', data)
      // Tip 定位选中卡片，设置选中样式
      this.cardIndex = index
      // Tip 设置右侧数据
      this.rightInfo = data
      // Tip 用来更新树节点
      this.treeUpdateKey++
      this.showTree = true
      // Tip 用来重置节点详情展示
      this.nodeLevel = 0
      // Tip 重置批次下拉列表展开状态
      this.showBatchNo = false
      // Tip 重置批次信息
      this.batchNoList = []
      // if (this.rightInfo.fileIds) {
      //   this.getFileListByIds(this.rightInfo.fileIds)
      // }
      console.log('4445555')
    },
    // Tip 加载子树数据
    async loadNode(node, resolve) {
      if (node.level === 0) {
        return resolve([...this.rightInfo.contractList])
      } else if (node.level === 1) {
        this.treeLoading = true
        const res = await requestWrapper(getStepContractApi({
          id: node.data.id,
          materialId: this.rightInfo.id
        }))
        if (!res.success) return
        this.treeLoading = false
        return resolve([...res.data])
      } else if (node.level === 2) {
        this.treeLoading = true
        const res = await requestWrapper(getStepOrderApi({
          id: node.data.id,
          materialId: this.rightInfo.id
        }))
        if (!res.success) return
        this.treeLoading = false
        return resolve([...res.data])
      }
    },
    // Tip 点击树节点
    handleNodeClick(data, node) {
      console.log(data, node)
      this.nodeLevel = node.level
      this.checkedNodeLevelData[node.level] = data
      this.getBatchNoList()
    },
    // Tip 获取批次号
    async getBatchNoList() {
      // 每次获取批次号需重置批次下拉列表展开状态
      this.showBatchNo = false
      if (this.nodeLevel) {
        const res = await requestWrapper(getBatchNoListApi({
          type: this.checkedNodeLevelData[this.nodeLevel].type,
          id: this.checkedNodeLevelData[this.nodeLevel].id,
          materialId: this.rightInfo.id
        }))
        if (!res.success) return
        this.batchNoList = res.data || []
      }
    },
    // Tip 获取文件列表
    getFileListByIds(fileIds) {
      return new Promise((resolve, reject) => {
        console.log('fielekekkekek', fileIds)
        const ids = fileIds.split(',')
        getUploadedFileListCommonApi({
          ids
        }).then(res => {
          const { code, data } = res
          if (!code) {
            console.log(data)
            const [item] = data
            if (item) {
              resolve(`${process.env.VUE_APP_BASE_API_WMS}${item.fileUrl}`)
              // this.imgUrl = `${process.env.VUE_APP_BASE_API_WMS}${item.fileUrl}`
              // this.getBase64(`${process.env.VUE_APP_BASE_API_WMS}${item.fileUrl}`, dataURL => {
              //   this.imgUrl = `${dataURL}`
              // })
            }
          }
        }).catch(err => {
          reject(err)
        })
      })
    },
    // Tip 跳转入库单详情
    toInWarehouseDetail(item) {
      this.$router.push({
        name: 'InStorageInfoDelivery',
        params: {
          orderId: item.stockInBillNumber,
          id: item.stockInBillId
        }
      })
    },
    // 展示大图
    showBigPic(imgs = []) {
      console.log('imgs:', imgs)
      this.$viewerApi({
        options: {
          toolbar: false,
          navbar: false,
          title: false
        },
        images: [...imgs]
      })
    },
    // 图片(url)转换base64
    getBase64(url, callback) {
      const Img = new Image()
      let dataURL = ''
      Img.src = url + '?v=' + Math.random()
      Img.setAttribute('crossOrigin', 'Anonymous')
      Img.onload = function() {
        const canvas = document.createElement('canvas')
        const width = Img.width
        const height = Img.height
        canvas.width = width
        canvas.height = height
        canvas.getContext('2d').drawImage(Img, 0, 0, width, height)
        dataURL = canvas.toDataURL('image/jpeg', 1) // 可选取多种模式
        return callback ? callback(dataURL) : null // 回掉函数获取Base64编码
      }
    }
    // 调用
    // let imgUrl=‘https://www.baidu.com/img/bd_logo1.png’
    //  this.getBase64(imgUrl, dataURL => {
    //  console.log(dataURL:就是base64了)
    // });
  }
}
</script>

<style lang="scss" scoped>
::v-deep .search-input-form-item {
  min-width: 215px;
  .el-form-item__content {
    width: 100%;
  }
}
::v-deep .el-empty-sty {
  height: calc(100vh - 140px - 140px);
  line-height: calc(100vh - 140px - 140px);
  .el-empty__image {
    display: none;
  }
}
::v-deep .box-card-rt {
  border: none;
  .el-card__header {
    padding: 18px 20px 0;
    border: none;
  }
}
.el-tree-sty {
  width: 50%;
  // border-right-style: solid;
  // border-right-width: 1px;
  // border-right-color: #cccccc;
}
::v-deep .box-card-rb {
  margin-top: 20px;
  border: none;
  .el-card__header {
    padding: 0px 20px;
    border: none;
  }
}
.barcode-box {
  display: flex;
  flex-wrap: wrap;
  // justify-content: space-between;
  box-sizing: border-box;
  padding: 5px;
  .el-link {
    width: 30%;
    margin-bottom: 15px;
  }
}
.barcode-box1 {
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
  .el-link {
    line-height: 1;
    padding-bottom: 2px;
  }
}
.img-box {
  position: relative;
  .image-slot {
    display: flex;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
  }
  .mask-layer {
    position: absolute;
    width: 100px;
    height: 100px;
    left: 0;
    top: 0;
    cursor: default;
    text-align: center;
    color: #fff;
    opacity: 0;
    font-size: 20px;
    background-color: rgba(0,0,0,.5);
    // transition: opacity .3s;
    .span {
      display: none;
      color: #FFF;
    }
  }
  &:hover {
    .mask-layer {
      opacity: 1;
      span {
        display: inline-block;
        font-size: 14px;
        cursor: pointer;
        margin-top: 36px;
      }
    }
  }
}
.expanded {
  transform: rotate(90deg);
}
</style>

