<!-- Tip 供应商Tabs标签页 -->
<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
        <el-form-item prop="id" class="search-input-form-item">
          <el-select
            v-model="query.id"
            filterable
            remote
            placeholder="请输入供应商编号或供应商名称"
            :remote-method="remoteMethod"
            :loading="selectLoading"
            class="filter-item"
            style="width: 100%;"
            clearable
            @clear="clearInfo"
          >
            <el-option
              v-for="item in options"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-button
          style="float: none !important;"
          class="filter-item"
          type="primary"
          icon="el-icon-search"
          @click="search"
        >查询</el-button>
      </el-form>
      <!-- Tip Main -->
      <!-- 拖拽屏幕 -->
      <template v-if="pageInfo">
        <drag-screen :extra-minus-height="140">
          <template #left>
            <el-card class="box-card" :body-style="{display: 'flex', alignItems: 'center', justifyContent: 'center', width: '100%', boxSizing: 'border-box'}">
              <el-descriptions :column="1" class="el-description-sty">
                <el-descriptions-item label="供应商编号">{{ pageInfo[0].code || '---' }}</el-descriptions-item>
                <el-descriptions-item label="供应商名称">{{ pageInfo[0].name || '---' }}</el-descriptions-item>
                <el-descriptions-item label="供应商简称">{{ pageInfo[0].abbName || '---' }}</el-descriptions-item>
              </el-descriptions>
            </el-card>
          </template>
          <template #right>
            <el-card class="box-card-rt" shadow="never">
              <div slot="header" class="clearfix">
                <span>全部订单</span>
              </div>
              <table-com
                ref="baseTableRef"
                :refresh-key="refreshKey"
                :table-height-type="550"
                :loading="loading"
                :index="true"
                :index-fixed="false"
                :selection="false"
                :multiple="false"
                :selection-fixed="false"
                :table-column-sort="false"
                :table-data="pageInfo[0].contractList || []"
                :column-list="columnList"
                :show-operate="showOperate"
                :operate-fixed="false"
                :table-selection="tableSelection"
                :operation-width="100"
                @selectionChange="selectionChange"
              />
            </el-card>
          </template>
        </drag-screen>
      </template>
      <template v-else>
        <el-empty class="el-empty-sty">
          <template slot="description">
            <el-button type="text" style="font-size: 23px;">
              暂无数据
            </el-button>
          </template>
        </el-empty>
      </template>
    </div>
  </div>
</template>

<script>
import DragScreen from '@/components/DragScreen'
import paginationPage from '@/mixins/paginationPage'
import { getSupplierInfoApi, getSupplierListApi } from '@/apis/modules/trace'
import { requestWrapper } from '@/utils/utils'
export default {
  name: 'SearchResultBatchNo',
  components: {
    DragScreen
  },
  mixins: [paginationPage],
  props: {
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      title: this.$route.meta.title,
      query: {
        id: ''
      },
      options: [],
      selectLoading: false, // 下拉选loading
      refreshKey: 1, // 表格唯一key
      showOperate: false, // 是否展示表格操作栏
      loading: false, // 表格loading
      tableData: [], // 表格数据
      tableSelection: [], // 表格选中数据
      columnList: [
        {
          module: 'word',
          prop: 'contractCode',
          label: '合同编号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'orderCode',
          label: '订单编号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'orderAmount',
          label: '订单金额（元）',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      pageInfo: null
    }
  },
  computed: {
  },
  mounted() {
  },
  methods: {
    init() {
      const params = {
        ...this.extraInfo,
        ...this.query
      }
      console.log('供应商tabs标签页接口参数：', params)
      return new Promise((resolve, reject) => {
        getSupplierInfoApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.pageInfo = data
              console.log('供应商tabs标签页接口响应：', data)
            }
            resolve(res)
          })
          .catch(err => {
            console.log('err', err)
            reject(err)
          })
      })
    },
    search() {
      if (!this.query.id) {
        this.pageInfo = null
        return
      }
      this.init()
    },
    // Tip 表格复选框改变回调
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    // 下拉框输入值发生变化时调用
    async remoteMethod(query) {
      if (query !== '') {
        this.selectLoading = true
        setTimeout(async() => {
          // Tip 远程下拉选项
          const res = await requestWrapper(getSupplierListApi({
            nameOrCode: query
          }))
          if (!res.success) return
          this.selectLoading = false
          this.options = res.data
        }, 200)
      } else {
        this.options = []
      }
    },
    clearInfo() {
      // console.log()
      this.options = []
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .search-input-form-item {
  min-width: 215px;
  .el-form-item__content {
    width: 100%;
  }
}
.el-description-sty {
  width: 70%;
}
::v-deep .el-empty-sty {
  height: calc(100vh - 140px - 140px);
  line-height: calc(100vh - 140px - 140px);
  .el-empty__image {
    display: none;
  }
}
::v-deep .box-card-rt {
  border: none;
  .el-card__header {
    padding: 18px 20px 0;
    border: none;
  }
}
</style>
