<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
        <el-form-item prop="nameOrCode" class="search-input-form-item">
          <el-input v-model="query.nameOrCode" placeholder="请输入条码号" class="filter-item" style="width: 100%;" clearable @keyup.enter.native="search" />
        </el-form-item>
        <el-button
          style="float: none !important;"
          class="filter-item"
          type="primary"
          icon="el-icon-search"
          @click="handleSearch"
        >查询</el-button>
      </el-form>
      <!-- 拖拽屏幕 -->
      <template v-if="pageInfo">
        <drag-screen :extra-minus-height="140">
          <template #left>
            <div v-watermark="pageInfo">
              <el-card class="box-card" :body-style="{display: 'flex', justifyContent: 'space-between',width: '100%', boxSizing: 'border-box'}">
                <!-- 物料信息左 -->
                <div class="img-box">
                  <el-image :src="imgUrl" style="width: 100px;height: 100px;">
                    <div slot="placeholder" class="image-slot">
                      加载中<span class="dot">...</span>
                    </div>
                    <div slot="error" class="image-slot">
                      <i class="el-icon-picture-outline" />
                    </div>
                  </el-image>
                  <span v-if="pageInfo.fileIds" class="mask-layer">
                    <span @click="showBigPic([imgUrl])">预览</span>
                  </span>
                </div>
                <!-- 物料信息右 -->
                <div style="width: calc(100% - 130px);height: 100%;position: relative;">
                  <div style="text-align: right;position: absolute;right: -10px;top: -20px;">
                    <el-button type="text" style="font-size: 18px;" @click="handleAbountDoc">相关文档</el-button>
                  </div>
                  <el-descriptions :column="1" style="height: 100%;overflow: auto;">
                    <el-descriptions-item label="物料编号">
                      {{ pageInfo.code || '---' }}
                    </el-descriptions-item>
                    <el-descriptions-item label="物料名称">
                      {{ pageInfo.name || '---' }}
                    </el-descriptions-item>
                    <el-descriptions-item label="规格型号">
                      {{ pageInfo.spec || '---' }}
                    </el-descriptions-item>
                    <el-descriptions-item label="评估类型">
                      {{ pageInfo.assessType_dictText || '---' }}
                    </el-descriptions-item>
                    <el-descriptions-item label="批次号">
                      {{ pageInfo.batchNo || '---' }}
                    </el-descriptions-item>
                    <el-descriptions-item label="条码号">
                      {{ pageInfo.barCode || '---' }}
                      <template v-if="pageInfo.deliveryReceiptType === '3'">
                        (<span :style="{color: contractType[pageInfo.deliveryReceiptType].color}">{{ `${contractType[pageInfo.deliveryReceiptType].text}` }}</span>)
                      </template>
                    </el-descriptions-item>
                    <el-descriptions-item label="序列号">
                      {{ pageInfo.snNo || '---' }}
                    </el-descriptions-item>
                    <el-descriptions-item label="数量">
                      {{ pageInfo.number || '---' }}
                    </el-descriptions-item>
                    <el-descriptions-item label="计量单位">
                      {{ pageInfo.unitName || '---' }}
                    </el-descriptions-item>
                  </el-descriptions>
                </div>
              </el-card>
            </div>
            <!-- 步骤条 -->
            <div style="height: 380px;margin-top: 15px;">
              <el-steps direction="vertical" :active="stepNum" class="my-el-steps" space="55px">
                <el-step :class="{checked: stepNum === 1}" :description="purchase ? `最近更新时间：${purchase.updateDate || '---'}` : ''" :status="purchase ? 'finish' : 'wait'" title="采购信息" @click.native="handleStepClick(1)">
                  <!-- <template #description>444</template> -->
                </el-step>
                <el-step :class="{checked: stepNum === 2}" :description="receipt ? `最近更新时间：${receipt.updateDate || '---'}` : ''" :status="receipt ? 'finish' : 'wait'" title="交接信息" @click.native="handleStepClick(2)">
                  <!-- <template #description>444</template> -->
                </el-step>
                <el-step :class="{checked: stepNum === 3}" :description="inspect ? `最近更新时间：${inspect.updateDate || '---'}` : ''" :status="inspect ? 'finish' : 'wait'" title="检验信息" @click.native="handleStepClick(3)">
                  <!-- <template #description>444</template> -->
                </el-step>
                <el-step :class="{checked: stepNum === 4}" :description="inWarehouse ? `最近更新时间：${inWarehouse.updateDate || '---'}` : ''" :status="inWarehouse ? 'finish' : 'wait'" title="入库信息" @click.native="handleStepClick(4)">
                  <!-- <template #description>444</template> -->
                </el-step>
                <el-step :class="{checked: stepNum === 5}" :description="outWarehouse ? `最近更新时间：${outWarehouse.updateDate || '---'}` : ''" :status="outWarehouse ? 'finish' : 'wait'" title="出库信息" @click.native="handleStepClick(5)">
                  <!-- <template #description>444</template> -->
                </el-step>
                <el-step :class="{checked: stepNum === 6}" :description="production ? `最近更新时间：${production.updateDate || '---'}` : ''" :status="production ? 'finish' : 'wait'" title="生产信息" @click.native="handleStepClick(6)">
                  <!-- <template #description>444</template> -->
                </el-step>
              </el-steps>
            </div>
          </template>
          <template #right>
            <purchase v-if="stepNum === 1" :extra-info="{...query, ...extraInfo, materialInfo: {...pageInfo}}" :purchase="purchase" />
            <receipt v-if="stepNum === 2" :extra-info="{...query, ...extraInfo, materialInfo: {...pageInfo}}" :receipt="receipt" @switchBarcode="switchBarcode" v-on="$listeners" />
            <inspect v-if="stepNum === 3" :extra-info="{...query, ...extraInfo, materialInfo: {...pageInfo}}" :inspect="inspect" />
            <in v-if="stepNum === 4" :extra-info="{...query, ...extraInfo, materialInfo: {...pageInfo}}" :in-warehouse="inWarehouse" />
            <out v-if="stepNum === 5" :extra-info="{...query, ...extraInfo, materialInfo: {...pageInfo}}" :out-warehouse="outWarehouse" />
            <production v-if="stepNum === 6" :extra-info="{...query, ...extraInfo, materialInfo: {...pageInfo}}" :production="production" />
          </template>
        </drag-screen>
      </template>
      <template v-else>
        <el-empty class="el-empty-sty">
          <template slot="description">
            <el-button type="text" style="font-size: 23px;">
              暂无数据
            </el-button>
          </template>
        </el-empty>
      </template>
    </div>
  </div>
</template>

<script>
import DragScreen from '@/components/DragScreen'
import { Purchase, Receipt, Inspect, In, Out, Production } from './components'
import { getBarCodeMaterialApi, getBarCodeTimelineInfoApi } from '@/apis/modules/trace'
import { getUploadedFileListCommonApi } from '@/apis/modules/common'
import { requestWrapper } from '@/utils/utils'
import scan from '@/mixins/scan.js'

export default {
  name: 'SearchResultBarcode',
  components: {
    DragScreen,
    Purchase,
    Receipt,
    Inspect,
    In,
    Out,
    Production
  },
  directives: {
    watermark: {
      inserted(el, binding) {
        const { inspectResult } = binding.value
        console.log('inspectResult: ', inspectResult)
        // 只有不合格才会展示水印
        if (inspectResult === false) {
          // 获取水印文字
          const text = '不合格'

          // 创建 canvas 元素
          const canvas = document.createElement('canvas')
          const ctx = canvas.getContext('2d')
          canvas.width = el.offsetWidth
          canvas.height = el.offsetHeight

          // 计算对角线的长度
          const diagonalLength = Math.sqrt(el.offsetWidth * el.offsetWidth + el.offsetHeight * el.offsetHeight)
          // 计算单个水印的宽度
          // const textWidth = ctx.measureText(text).width
          const gapWidth = diagonalLength / (text.length * 2)
          const fontSize = gapWidth * 0.7 // 字体大小根据间隔调整

          ctx.font = `bold ${fontSize}px Arial` // 设置字体样式(大小和类型)
          ctx.fillStyle = 'rgba(255, 0, 0, 0.3)' // 设置水印文字颜色
          ctx.textAlign = 'center' // 设置水印文本对齐方式
          ctx.textBaseline = 'middle'

          // 绘制水印
          ctx.fillText(text[0], fontSize / 1.5, fontSize / 1.2)
          ctx.fillText(text[1], el.offsetWidth / 2, el.offsetHeight / 2)
          ctx.fillText(text[2], el.offsetWidth - fontSize / 1.5, el.offsetHeight - fontSize / 1.5)
          // for (let i = 1; i <= text.length; i++) {
          //   const textX = el.offsetWidth / 2 + (i - text.length / 2) * gapWidth
          //   const textY = el.offsetHeight / 2 + (i - text.length / 2) * gapWidth
          //   ctx.fillText(text[i - 1], textX, textY)
          // }

          // ctx.rotate(Math.atan(canvas.heightw / canvas.width)) // 旋转当前图形
          // ctx.fillText(text, canvas.width, 23) // 在canvas上绘制文本(在坐标(0,0)处绘制文本)

          // 创建 div 覆盖在元素上
          const div = document.createElement('div')
          div.style.width = `${el.offsetWidth}px`
          div.style.height = `${el.offsetHeight}px`
          div.style.position = 'absolute'
          div.style.top = 0
          div.style.left = 0
          div.style.right = 0
          div.style.bottom = 0
          div.style.pointerEvents = 'none' // 防止阻止下层元素的交互
          div.style.background = `url('${canvas.toDataURL('image/png')}') no-repeat  center center`
          div.style.backgroundSize = '100% 100%' // 确保背景图片完全覆盖 div
          // 添加 div 到目标元素
          el.style.position = 'relative'
          el.appendChild(div)
        }
      }
    }
  },
  mixins: [scan],
  props: {
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      title: this.$route.meta.title,
      query: {
        nameOrCode: ''
      },
      stepNum: 1,
      pageInfo: null,
      imgUrl: '',
      purchase: null, // 采购-信息
      receipt: null, // 交接-信息
      inspect: null, // 检验-信息
      inWarehouse: null, // 入库-信息
      outWarehouse: null, // 出库-信息
      production: null, // 生产-信息
      contractType: {
        '1': {
          text: '采购',
          color: '#2ec7c9'
        },
        '2': {
          text: '外协',
          color: '#b6a2de'
        },
        '3': {
          text: '寄售',
          color: '#5ab1ef'
        },
        '4': {
          text: '成品',
          color: '#ffb980'
        }
      }
    }
  },
  computed: {
  },
  mounted() {
  },
  methods: {
    init() {
      const params = {
        ...this.extraInfo,
        ...this.query
      }
      this.pageInfo = null
      console.log('条码号tabs标签页接口参数：', params)
      return new Promise((resolve, reject) => {
        getBarCodeMaterialApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.pageInfo = data
              if (this.pageInfo && this.pageInfo.fileIds) {
                this.getFileListByIds(this.pageInfo.fileIds)
              }
              console.log('条码号tabs标签页接口响应：', data)
            }
            resolve(res)
          })
          .catch(err => {
            console.log('err', err)
            reject(err)
          })
      })
    },
    // 条码号回车查询
    search() {
      this.handlePublicFn({ barCode: this.query.nameOrCode })
    },
    // 扫码结束
    scanEnd(data) {
      this.handlePublicFn(JSON.parse(data))
    },
    handlePublicFn(obj) {
      const arr = obj.barCode.split(':')
      this.query.nameOrCode = arr.length > 1 ? arr[1] : arr[0]
      this.handleSearch()
    },
    handleStepClick(num) {
      this.stepNum = num
      console.log('stepNum', num)
    },
    // 获取文件列表
    getFileListByIds(fileIds) {
      return new Promise((resolve, reject) => {
        const ids = fileIds.split(',')
        getUploadedFileListCommonApi({
          ids
        }).then(res => {
          const { code, data } = res
          if (!code) {
            console.log(data)
            const [item] = data
            if (item) {
              this.imgUrl = `${process.env.VUE_APP_BASE_API_WMS}${item.fileUrl}`
              // this.getBase64(`${process.env.VUE_APP_BASE_API_WMS}${item.fileUrl}`, dataURL => {
              //   this.imgUrl = `${dataURL}`
              // })
            }
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 获取时间轴信息(主要获取时间轴上的更新时间，以此区分是否进展到了本步轴)
    async getBarCodeTimeLineInfo() {
      const params = {
        ...this.extraInfo,
        ...this.query
      }
      const res = await requestWrapper(getBarCodeTimelineInfoApi(params))
      if (!res.success) return
      for (const key in res.data) {
        this[key] = res.data[key]
      }
    },
    // 查询
    handleSearch() {
      if (!this.query.nameOrCode) {
        this.pageInfo = null
        return
      }
      this.init()
      this.getBarCodeTimeLineInfo()
      this.stepNum = 0
      this.$nextTick(() => {
        this.handleStepClick(1)
      })
    },
    // 条码号tabPanel->交接信息->全部条码 (条码切换)
    switchBarcode({ barCode }) {
      this.query.nameOrCode = barCode
      this.handleSearch()
    },
    // 展示大图
    showBigPic(imgs = []) {
      console.log('imgs:', imgs)
      this.$viewerApi({
        options: {
          toolbar: false,
          navbar: false,
          title: false
        },
        images: [...imgs]
      })
    },
    // 相关文档
    handleAbountDoc() {
      this.$router.push({
        name: 'RelatedDocument',
        params: {
          barcode: this.query.nameOrCode
        }
      })
    },
    // 图片(url)转换base64
    getBase64(url, callback) {
      const Img = new Image()
      let dataURL = ''
      Img.src = url + '?v=' + Math.random()
      Img.setAttribute('crossOrigin', 'Anonymous')
      Img.onload = function() {
        const canvas = document.createElement('canvas')
        const width = Img.width
        const height = Img.height
        canvas.width = width
        canvas.height = height
        canvas.getContext('2d').drawImage(Img, 0, 0, width, height)
        dataURL = canvas.toDataURL('image/jpeg', 1) // 可选取多种模式
        return callback ? callback(dataURL) : null // 回掉函数获取Base64编码
      }
    }
    // 调用
    // let imgUrl=‘https://www.baidu.com/img/bd_logo1.png’
    //  this.getBase64(imgUrl, dataURL => {
    //  console.log(dataURL:就是base64了)
    // });

  }
}
</script>

<style lang="scss" scoped>
::v-deep .search-input-form-item {
  min-width: 215px;
  .el-form-item__content {
    width: 100%;
  }
}
::v-deep .el-empty-sty {
  height: calc(100vh - 140px - 140px);
  line-height: calc(100vh - 140px - 140px);
  .el-empty__image {
    display: none;
  }
}
::v-deep .my-el-steps {
  .el-step {
    cursor: pointer;
    flex-basis: auto;
    .el-step__main {
      .el-step__description {
        // margin-top: 0;
      }
    }
    &.checked {
      background-color: #C3E2F3;
      opacity: .8;
    }
  }
}
.img-box {
  // width: 100px;
  // height: 100px;
  position: relative;
  .image-slot {
    display: flex;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
  }
  .mask-layer {
    position: absolute;
    width: 100px;
    height: 100px;
    left: 0;
    top: 0;
    cursor: default;
    text-align: center;
    color: #fff;
    opacity: 0;
    font-size: 20px;
    background-color: rgba(0,0,0,.5);
    // transition: opacity .3s;
    .span {
      display: none;
      color: #FFF;
    }
  }
  &:hover {
    .mask-layer {
      opacity: 1;
      span {
        display: inline-block;
        font-size: 14px;
        cursor: pointer;
        margin-top: 36px;
      }
    }
  }
}
</style>

