
export default {
  data() {
    return {
      lastTime: undefined, // 主要标记扫码开始和区分键盘输入
      nextTime: undefined, //
      lastCode: undefined,
      nextCode: undefined,
      code: ''
    }
  },
  mounted() {
    document.addEventListener('keypress', this.scan, false)
  },
  methods: {
    scan(e) {
      if (this.scanDisabled) {
        return
      }
      if (window.event) { // IE
        this.nextCode = e.keyCode
      } else if (e.which) { // Netscape/Firefox/Opera
        this.nextCode = e.which
      }
      // console.time()
      if (e.which === 13) { // 扫码枪结束时的键值为13
        if (this.code.length < 3) return // 手动输入的时间不会让code的长度大于2，所以这里只会对扫码枪有
        console.log('扫码结束')
        // console.timeEnd()
        if (!this.code.startsWith('{')) {
          const ind = this.code.indexOf('{')
          this.code = this.code.substring(ind)
        }
        console.log(this.code, 'code')
        this.scanEnd(this.code) // 调用页面中的事件，返回扫描结果
        // that.parseQRCode(code) // 获取到扫码枪输入的内容，做别的操作
        this.code = ''
        this.lastCode = ''
        this.lastTime = ''
        // this.disabled = false // disabled 为预留的参数，可用于打印机
      }
      this.nextTime = new Date().getTime()
      if (!this.lastTime && !this.lastCode) {
        // this.disabled = true
        console.log('扫码开始。。。')
        this.code += e.key
      }
      // 区分键盘与扫描枪 - 暂时设置两次键盘输入时间不大于30ms 小于20 会发生组合键被区分 如{ 解析成 Shift 和 [    太长了没法区分键盘输入
      // 现在的问题是可以扫码时，同时按多个键盘上的键，可能会有问题
      if (this.lastCode && this.lastTime && this.nextTime - this.lastTime > 30) {
        this.code = e.key
      } else if (this.lastCode && this.lastTime) {
        console.log('扫码中。。。')
        this.code += e.key
      }
      this.lastCode = this.nextCode
      this.lastTime = this.nextTime
    }
  },
  beforeDestroy() {
    document.removeEventListener('keypress', this.scan, false)
  }
}
