<template>
  <el-dialog
    :title="title"
    top="5vh"
    :visible="value"
    :show-close="true"
    width="920px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar class="scroll-dialog">
      <div class="main-box">
        <el-row :gutter="10">
          <el-col :span="8">
            <el-table height="550" :data="tableData" border :header-cell-style="{background:'#eef1f6',color:'#606266'}" highlight-current-row :header-row-style="{height: '36px'}" cell-class-name="table_cell" style="width: 100%" @row-click="handleRowClick">
              <el-table-column align="center" prop="materialName" label="物料名称" />
            </el-table>
          </el-col>
          <el-col :span="8">
            <el-table height="550" :data="tableData1" border :header-cell-style="{background:'#eef1f6',color:'#606266'}" highlight-current-row :header-row-style="{height: '36px'}" cell-class-name="table_cell" style="width: 100%" @row-click="handleRowClick1">
              <el-table-column align="center" prop="batchNo" label="批次号" />
            </el-table>
          </el-col>
          <el-col :span="8">
            <el-table height="550" :data="tableData2" border :header-cell-style="{background:'#eef1f6',color:'#606266'}" highlight-current-row :header-row-style="{height: '36px'}" cell-class-name="table_cell" style="width: 100%" @row-click="handleRowClick2">
              <el-table-column align="center" prop="barCode" label="条码号" />
            </el-table>
          </el-col>
        </el-row>
      </div>
    </el-scrollbar>
    <div slot="footer">
      <el-button type="primary" @click="confirmDialog">确 定</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { getSerialNoDlgListApi } from '@/apis/modules/trace/'
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      tableData: [],
      tableData1: [],
      tableData2: [],
      rowInfo: null,
      rowInfo1: null,
      rowInfo2: null,
      barcodeInfo: null
    }
  },
  methods: {
    init() {
      const params = {
        ...this.extraInfo
      }
      return new Promise((resolve, reject) => {
        getSerialNoDlgListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.tableData = data
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 物料点击
    handleRowClick(row) {
      if (!this.rowInfo) {
        this.rowInfo = row
        this.tableData1 = row.batchNoList
      } else {
        if (this.rowInfo.materialId !== row.materialId) {
          this.rowInfo = row
          this.tableData1 = row.batchNoList
          this.tableData2 = []
          this.rowInfo2 = []
        }
      }
    },
    // 批次号点击
    handleRowClick1(row) {
      this.rowInfo1 = row
      this.tableData2 = row.batcodeList
    },
    // 条码号点击
    handleRowClick2(row) {
      this.rowInfo2 = row
      this.barcodeInfo = row
    },
    // 打开初始化
    initSelect() {
      this.rowInfo = null
      this.rowInfo1 = null
      this.rowInfo2 = null
      this.tableData = []
      this.tableData1 = []
      this.tableData2 = []
      this.barcodeInfo = null // 清空上次选中的条码信息
      this.init()
    },
    // 关闭
    beforeClose(done) {
      this.$emit('input', false)
      typeof done === 'function' && done()
    },
    // 重置
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.init()
    },
    // 确定
    confirmDialog() {
      if (!this.barcodeInfo) {
        this.$message({
          message: '请选择条码号',
          type: 'error'
        })
        return
      }
      this.$emit('confirm', this.barcodeInfo)
      this.beforeClose()
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
  height: 600px;
}
.main-box {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
 ::v-deep .el-dialog__body {
  padding-top: 10px;
  padding-bottom: 0;
}
</style>
