<template>
  <div
    v-if="production"
    v-loading="loading"
    class="main-box"
    element-loading-text="加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <template v-if="pageInfo">
      <el-card class="box-card-rt" shadow="never">
        <div slot="header" class="clearfix">
          <span>生产信息</span>
        </div>
        <el-tree
          :data="treeData"
          :props="treeProps"
          @node-click="handleNodeClick"
        />
      </el-card>
    </template>
  </div>
  <el-empty v-else class="empty-sty">
    <template slot="description">
      <el-button type="text" style="font-size: 23px;">
        暂无生产信息
      </el-button>
    </template>
  </el-empty>
</template>

<script>
export default {
  name: 'Production',
  components: {
  },
  mixins: [],
  props: {
    extraInfo: {
      type: Object,
      default: () => ({})
    },
    production: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      title: this.$route.meta.title,
      treeData: [{
        label: '一级 1',
        children: [{
          label: '二级 1-1',
          children: [{
            label: '三级 1-1-1'
          }]
        }]
      }, {
        label: '一级 2',
        children: [{
          label: '二级 2-1',
          children: [{
            label: '三级 2-1-1'
          }]
        }, {
          label: '二级 2-2',
          children: [{
            label: '三级 2-2-1'
          }]
        }]
      }, {
        label: '一级 3',
        children: [{
          label: '二级 3-1',
          children: [{
            label: '三级 3-1-1'
          }]
        }, {
          label: '二级 3-2',
          children: [{
            label: '三级 3-2-1'
          }]
        }]
      }],
      treeProps: {
        children: 'children',
        label: 'label'
      },
      pageInfo: null,
      loading: false
    }
  },
  computed: {
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.loading = true
      setTimeout(() => {
        this.loading = false
      }, 500)
    },
    handleNodeClick(data) {
      console.log(data)
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .box-card-rt {
  border: none;
  .el-card__header {
    padding: 18px 20px 0;
    border: none;
  }
}
.main-box {
  min-height: 620px;
  padding: 10px;
  box-sizing: border-box;
}
.empty-sty {
  height: 580px;
  line-height: 580px;
  padding: 0;
  ::v-deep .el-empty__image {
    display: none;
  }
}
</style>
