<template>
  <div
    v-if="inspect"
    v-loading="loading"
    class="main-box"
    element-loading-text="加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <el-card v-if="pageInfo" class="box-card-rt" shadow="never">
      <div slot="header" class="clearfix">
        <span>检验信息</span>
      </div>
      <el-descriptions :column="3">
        <el-descriptions-item label="检验单号">
          {{ pageInfo.inspectNo || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="检验单状态">
          <template v-if="pageInfo.status || pageInfo.status >= 0">
            <el-tag :type="statusMap[`${pageInfo.status}`]" effect="plain">
              {{ statusChartMap[`${pageInfo.status}`] || '---' }}
            </el-tag>
          </template>
          <template v-else>---</template>
        </el-descriptions-item>
        <el-descriptions-item label="检验人">
          {{ pageInfo.inspector || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="创建时间">
          {{ pageInfo.createDate || '---' }}
        </el-descriptions-item>
        <el-descriptions-item label="检验完成时间">
          {{ pageInfo.finishDate || '---' }}
        </el-descriptions-item>
      </el-descriptions>
      <el-descriptions :column="3">
        <el-descriptions-item label="检验方式">
          {{ pageInfo.inspectType_dictText || '' }}
        </el-descriptions-item>
        <el-descriptions-item label="检验数量">
          <template v-if="pageInfo.inspectNumber">
            {{ typeof pageInfo.inspectNumber === 'number' ? pageInfo.inspectNumber : '---' }}
          </template>
          <template v-else>---</template>
        </el-descriptions-item>
      </el-descriptions>
      <el-descriptions :column="3">
        <el-descriptions-item label="合格数量">
          <template v-if="pageInfo.qualifiedNumber">
            {{ typeof pageInfo.qualifiedNumber === 'number' ? pageInfo.qualifiedNumber : '---' }}
          </template>
          <template v-else>---</template>
        </el-descriptions-item>
        <!-- <el-descriptions-item label="让步数量">
            <template v-if="pageInfo.compromiseNumber || pageInfo.compromiseNumber >= 0">
              {{ typeof pageInfo.compromiseNumber === 'number' ? pageInfo.compromiseNumber : '---' }}
            </template>
            <template v-else>---</template>
          </el-descriptions-item> -->
        <el-descriptions-item label="不合格数量">
          <template v-if="pageInfo.unqualifiedNumber || pageInfo.unqualifiedNumber >= 0">
            {{ typeof pageInfo.unqualifiedNumber === 'number' ? pageInfo.unqualifiedNumber : '---' }}
          </template>
          <template v-else>---</template>
        </el-descriptions-item>
      </el-descriptions>
      <div style="height: 1px;background-color: #ECEFF5;" />
      <div class="fl clearfix" style="margin-top: 20px;">
        <!-- Tip 需要控制按钮的操作状态 -->
        <el-button type="primary" :disabled="pageInfo.unqualifiedNumber <= 0">不合格品报告</el-button>
        <el-button type="primary" @click="handleOpenPDF">检验档案</el-button>
      </div>
    </el-card>
  </div>
  <el-empty v-else class="empty-sty">
    <template slot="description">
      <el-button type="text" style="font-size: 23px;">
        暂无检验信息
      </el-button>
    </template>
  </el-empty>
</template>

<script>
import { getBarCodeInspectApi } from '@/apis/modules/trace'
import { statusMap, statusChartMap } from '@/views/in-storage/inspection/status'
export default {
  name: 'Inspection',
  components: {
  },
  mixins: [],
  props: {
    extraInfo: {
      type: Object,
      default: () => ({})
    },
    inspect: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      title: this.$route.meta.title,
      pageInfo: null,
      statusMap,
      statusChartMap,
      loading: false
    }
  },
  computed: {
  },
  mounted() {
    this.init()
  },
  dicts: [],
  methods: {
    init() {
      const params = {
        ...this.extraInfo
      }
      console.log('条码号tabs标签页-检验信息接口参数：', params)
      this.loading = true
      return new Promise((resolve, reject) => {
        getBarCodeInspectApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.pageInfo = data
              console.log('条码号tabs标签页-检验信息接口响应：', data)
            }
            this.loading = false
            resolve(res)
          })
          .catch(err => {
            console.log('err', err)
            this.loading = false
            reject(err)
          })
      })
    },
    handleOpenPDF() {
      window.open(process.env.VUE_APP_BASE_API_WMS + this.pageInfo.report)
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .box-card-rt {
  border: none;
  .el-card__header {
    padding: 18px 20px 0;
    border: none;
  }
}
.main-box {
  min-height: 620px;
  padding: 10px;
  box-sizing: border-box;
}
.empty-sty {
  height: 580px;
  line-height: 580px;
  padding: 0;
  ::v-deep .el-empty__image {
    display: none;
  }
}
</style>
